@import "../helpers/_FontMaster";
.subscription-bg {
  background-image: url(../../media/subscription-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center bottom;
}
.subscribe-header {
  font-family: $font-display;
  font-size: 2.2em;
  font-weight: 700;
  font-style: normal;
  line-height: 1.8;
  color: $brown-text;
  letter-spacing: 0em;
}
a:hover {
  text-decoration: none;
}

// add style for cart page
.cart-bg {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  background-image: url(../../media/cart-bg.jpg);
  /* border-color: #f8f9fa; */
  background-size: cover;
  /* background-position: left top; */
  background-repeat: no-repeat;
  // background-attachment: fixed;
  // height: 100vh;
  /* width: auto; */
  /* align-items: center; */
  // margin-bottom: 0;
  .page-title {
    font-family: Lato, Helvetica, Arial, sans-serif;
    font-size: 40px;
    font-weight: 900;
    font-style: normal;
    letter-spacing: 0.1em;
    line-height: 1;
    color: #333;
  }
  .sub-title {
    font-size: 16px;
    padding: 30px 0 0;
    font-family: "Playfair Display";
    font-style: italic;
    line-height: 1;
    color: #333;
    letter-spacing: 0.2em;
  }
}
.cart-table {
  .sub-total {
    font-weight: 600;
    font-size: 18px;
    font-family: "Playfair Display";
    font-style: italic;
    color: black;
  }
  .product-name {
    font-size: 18px;
    font-weight: 700;
    font-family: "Playfair Display";
    letter-spacing: 0.05em;
    color: #333;
  }
  .product-price {
    font-size: 15px;
    font-family: "Playfair Display";
    font-style: italic;
    color: #696969;
  }
  .product-quantity {
    font-size: 15px;
    color: #404040;
    font-family: Lato;
  }
  .product-image {
    // width: 100px;
    // height: 100px;
    // padding: 10px;
    border: 1px solid #f4f4f4;
  }
}
.cart-checkout {
  .cart-total {
    border-width: 0 0 1px;
    border-style: solid;
    border-color: #f4f4f4;
    text-transform: uppercase;
    font-size: 15px;
    color: #333;
    font-weight: 700;
    font-family: Lato, Helvetica, Arial, sans-serif;
    // font-weight: 400;
    font-style: normal;
    letter-spacing: 0em;
  }
}
// add style login page
.login-container {
  background: #007bff;
  background: linear-gradient(to right, #0062e6, #33aeff);
  --input-padding-x: 1.5rem;
  --input-padding-y: 0.75rem;
  input:hover {
    cursor: pointer;
  }
  label:hover {
    cursor: pointer;
  }
  .card-signin {
    border: 0;
    border-radius: 1rem;
    box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);
  }

  .card-signin .card-title {
    margin-bottom: 2rem;
    font-weight: 300;
    font-size: 1.5rem;
  }

  .card-signin .card-body {
    padding: 2rem;
  }

  .form-signin {
    width: 100%;
  }

  .form-signin .btn {
    font-size: 80%;
    border-radius: 5rem;
    letter-spacing: 0.1rem;
    font-weight: bold;
    padding: 1rem;
    transition: all 0.2s;
  }

  .form-label-group {
    position: relative;
    margin-bottom: 1rem;
  }

  .form-label-group input {
    height: auto;
    border-radius: 2rem;
  }

  .form-label-group > input,
  .form-label-group > label {
    padding: var(--input-padding-y) var(--input-padding-x);
  }

  .form-label-group > label {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    margin-bottom: 0;
    /* Override default `<label>` margin */
    line-height: 1.5;
    color: #495057;
    border: 1px solid transparent;
    border-radius: 0.25rem;
    transition: all 0.1s ease-in-out;
  }

  .form-label-group input::-webkit-input-placeholder {
    color: transparent;
  }

  .form-label-group input:-ms-input-placeholder {
    color: transparent;
  }

  .form-label-group input::-ms-input-placeholder {
    color: transparent;
  }

  .form-label-group input::-moz-placeholder {
    color: transparent;
  }

  .form-label-group input::placeholder {
    color: transparent;
  }

  .form-label-group input:not(:placeholder-shown) {
    padding-top: calc(
      var(--input-padding-y) + var(--input-padding-y) * (2 / 3)
    );
    padding-bottom: calc(var(--input-padding-y) / 3);
  }

  .form-label-group input:not(:placeholder-shown) ~ label {
    padding-top: calc(var(--input-padding-y) / 3);
    padding-bottom: calc(var(--input-padding-y) / 3);
    font-size: 12px;
    color: #777;
  }

  .btn-google {
    color: white;
    background-color: #ea4335;
  }

  .btn-facebook {
    color: white;
    background-color: #3b5998;
  }

  /* Fallback for Edge
-------------------------------------------------- */

  @supports (-ms-ime-align: auto) {
    .form-label-group > label {
      display: none;
    }
    .form-label-group input::-ms-input-placeholder {
      color: #777;
    }
  }

  /* Fallback for IE
-------------------------------------------------- */

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    .form-label-group > label {
      display: none;
    }
    .form-label-group input:-ms-input-placeholder {
      color: #777;
    }
  }
}
